.navbar {
  grid-column: 2/3;
  height: 8vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: sticky;
  top: 0;
}

.navbarItemsContainer {
  width: 90%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.navbarItemsContainer > h2 {
  font-size: 2rem;
  cursor: pointer;
}

.navButtons > .navButton {
  cursor: pointer;
  transform: scale(2);
  margin: 0 20px;
}

.navButtons > .navButton:hover {
  color: rgb(55, 55, 55);
}

.navButtons > .navButton:active {
  color: rgb(110, 110, 110);
}
